<template>
  <div class="slider" ref="slider">
    <div class="slider-group" ref="sliderGroup">
      <slot>
      </slot>
    </div>
    <div class="dots">
      <span class="dot" v-for="(item, index) in dots" :class="{active: currentPageIndex === index }" ></span>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
  import {addClass} from 'common/js/dom'
  import BScroll from 'better-scroll'

  export default {
    name: 'slider',
    props: {
      loop: {//滑动
        type: Boolean,
        default: true
      },
      autoPlay: { //自动轮播
        type: Boolean,
        default: true
      },
      interval: { //轮播时间
        type: Number,
        default: 4000
      }
    },
    data() {
      return {
        dots: [],//点组
        currentPageIndex: 0 //点的位置
      }
    },
    mounted(){
      setTimeout(()=>{
        this._setSliderWidth() //设置宽度
        this._initDots() //滑动点
        this._initSlider() //滑动

        if (this.autoPlay) {//自动轮播
          this._play()
        }
      },20)
      //窗口适应
      window.addEventListener('resize', () => {
        if (!this.slider) {
          return
        }
        this._setSliderWidth(true)
        this.slider.refresh()//宽度变化,重新计算
      })
    },

    methods:{
      _setSliderWidth(isResize){
        this.children = this.$refs.sliderGroup.children
        let width = 0
        let sliderWidth = this.$refs.slider.clientWidth
        for (let i = 0; i < this.children.length; i++) {
          let child = this.children[i]
          addClass(child, 'slider-item')
          child.style.width = sliderWidth + 'px'
          width += sliderWidth
        }
        if (this.loop&&!isResize) {
          width += 2 * sliderWidth //为了实现无缝滚动
        }
        this.$refs.sliderGroup.style.width = width + 'px'
      },
      _initDots(){
        this.dots = new Array(this.children.length)
      },
      _initSlider() {
        this.slider = new BScroll(this.$refs.slider, {
          scrollX: true,
          scrollY: false,
          momentum:false,//根据滑动的距离和时间计算出动量
          snap: {
            loop:this.loop,//无缝滚动 需要加2个子宽度
            threshold: 0.4//表示可滚动到下一个的阈值
          },
          snapSpeed: 400,//下一个的时间
          click: true
        })
        //切换下一张调用
        this.slider.on('scrollEnd',()=>{
          let pageIndex=this.slider.getCurrentPage().pageX;
          this.currentPageIndex=pageIndex;//点的index
          if (this.autoPlay) {
            clearTimeout(this.timer);
            this._play();
          }
        })

      },

      _play() {
//        let pageIndex = this.currentPageIndex + 1;
//        if (this.loop) {
//          pageIndex += 1;
//        }
        //console.log(pageIndex)
        this.timer = setTimeout(() => {
          this.slider.next()
        }, this.interval);
      }
    },

    destroyed(){
      clearTimeout(this.timer)
    }
  }

</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  .slider
    min-height: 1px
    .slider-group
      position: relative
      overflow: hidden
      white-space: nowrap
      .slider-item
        float: left
        box-sizing: border-box
        overflow: hidden
        text-align: center
        a
          display: block
          width: 100%
          overflow: hidden
          text-decoration: none
        img
          display: block
          width: 100%
    .dots
      position: absolute
      right: 0
      left: 0
      bottom: 12px
      text-align: center
      font-size: 0
      .dot
        display: inline-block
        margin: 0 4px
        width: 8px
        height: 8px
        border-radius: 50%
        background: $color-text-l
        &.active
          width: 20px
          border-radius: 5px
          background: $color-text-ll
</style>



